.apology-section {
	position: relative;
	background-color: var(--color-primary);
	border-radius: var(--border-radius);
	box-shadow: inset 0 8px 1rem rgba($color: #000000, $alpha: 0.15);
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, 0);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(40%, -20%) scaleX(-1);
		@include min-md {
			width: 260px;
		}
		@include min-lg {
			width: 280px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 140px;
		transform: translate(-40%, 0%) rotate(20deg);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 140px;
		transform: translate(60%, 0) scaleX(-1) rotate(20deg);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-5 {
		position: relative;
		width: 120px;
		margin: 2rem auto -10rem;
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.appology-wrap {
		position: relative;
		padding: 5rem 0;
		overflow: hidden;
		@include max-md {
			padding: 3rem 0;
		}
	}
}
